import React, { useState, useContext } from 'react';
import { useForm, Controller } from "react-hook-form";
import styled from 'styled-components'
import { SettingContext } from '../context/SettingContext'

import NavBarForCheck from '../components/NavBarForCheck'
import { Radio, RadioGroup, FormControlLabel} from '@material-ui/core';
import NotificationsIcon from '@material-ui/icons/Notifications';

import { db } from '../config/firebaseConfig'

const SelfCheckPage = () => {
  const [ setting ] = useContext(SettingContext)
  const [ userInfo, setUserInfo ] = useState({
    name: "",
    position: "",
    title: "",
    serviceTime: "",
    reservationDate: ""
  })
  const [ open, setOpen ] = useState(false)
  const [ result, setResult ] = useState(null)
  const { register, handleSubmit, errors, control } = useForm()

  const onSubmit = (data) => {
    const { name, position, worship } = data
    let title = ""
    let serviceTime = ""
    let reservationDate = ""
    if (worship.substring(0,4) === '') {
      reservationDate = setting.sunday
      if (worship === '1') {
        title = worship.substring(0, 4)
        serviceTime = worship.substring(4, 6)
      } else if (worship === '2') {
        title = worship.substring(0, 4)
        serviceTime = worship.substring(4, 6)
      } else if (worship === '3') {
        title = worship.substring(0, 4)
        serviceTime = worship.substring(4, 6)
      } else if (worship === '4') {
        title = worship.substring(0, 4)
        serviceTime = worship.substring(4, 6)
      } else if (worship === '6') {
        title = worship.substring(0, 4)
        serviceTime = worship.substring(4, 6)
      }
    } else {
      title = worship
      serviceTime = '1'
      if (worship === '') {
        reservationDate = setting.wednesday
      } else if (worship === '') {
        reservationDate = setting.friday
      }
    }

    setUserInfo({
      name: name,
      position: position,
      title: title,
      serviceTime: serviceTime,
      reservationDate: reservationDate
    })
    checkResult( name, position, title, serviceTime, reservationDate )
    setOpen(true)
  }

  const onCloseClick = () => {
    setResult(null)
    setOpen(false)
  }

  const checkResult = ( name, position, title, serviceTime, reservationDate ) => {
    const userListRef = db.collection(title).doc(serviceTime).collection(reservationDate)
    let indexQuery = userListRef.where("index", "==", 90)
    let query = userListRef.where("name", "==", name).where("position", "==", position);
    query.get().then(querySnapshot => {
      if (querySnapshot.empty) {
        console.log('DB ')
        setResult('')
      } else {
        querySnapshot.forEach((doc) => {
          if (doc.data().index < 71) {
            console.log(' ')
            setResult('')
          } else if (doc.data().index >= 71 && doc.data().index < 91) {
            if (doc.data().index === 90) {
              indexQuery.get().then((querySnapshot) => {
                let tempArray = []
                querySnapshot.forEach((doc) => {
                  tempArray.push({
                   id: doc.id,
                   ...doc.data()
                  })
               })
               tempArray.sort((a, b) => {
                 return a.serverTime - b.serverTime
               })
               if (tempArray[0].name === name) {
                console.log('90  ')
                setResult('')
               } else {
                console.log('90  ')
                setResult('')
               }
              })
            } else {
              console.log(' ')
              setResult('')
            }
          } else {
            console.log('90  ')
            setResult('')
          }
        })
      }
    })
  }

  return (
    <Container>
      <NavBarForCheck />
      <Header> </Header>

      <CommentContainer>
        <CommentHeader>
          <NotificationsIcon style={{ marginRight: '0.5rem'}}/>
          <div></div>
        </CommentHeader>
        <CommentBody>
          <div style={{ marginBottom: '0.25rem' }}>   </div>
          <div>     </div>
          <div>   .</div>
        </CommentBody>
      </CommentContainer>

      {!open ? (
        <InserForm onSubmit={handleSubmit(onSubmit)} style={{display:'flex', flexDirection:'column'}}>
          <InputContainer>
            <Title></Title>
            <Input name="name" placeholder=" " ref={register({ required: true, maxLength: 4 })}/>
            {errors.name && <div style={{color: 'red', marginTop: 5, marginLeft: 5}}>   </div>}
          </InputContainer>
          <InputContainer>
            <Title></Title>
            <Input name="position" placeholder="/////" ref={register({ required: true})} />
            {errors.position && <div style={{color: 'red', marginTop: 5, marginLeft: 5}}> </div>}
          </InputContainer>
          <InputContainer>
            <Title>  </Title>
            <Controller
              as={
                <RadioGroup aria-label="" style={{paddingLeft: '1rem'}} >
                  {setting.isWeekday ? (
                    <>
                    <FormControlLabel
                      value=""
                      control={<Radio />}
                      label=""
                    />
                    <FormControlLabel
                      value=""
                      control={<Radio />}
                      label=""
                    />
                    </>
                  ) : (
                    <>
                    <FormControlLabel
                    value="1"
                    control={<Radio />}
                    label="1"
                  />
                  <FormControlLabel
                    value="2"
                    control={<Radio />}
                    label="2"
                  />
                  <FormControlLabel
                    value="3"
                    control={<Radio />}
                    label="3"
                  />
                  <FormControlLabel
                    value="4"
                    control={<Radio />}
                    label="4"
                  />
                  <FormControlLabel
                    value="6"
                    control={<Radio />}
                    label="6"
                  />
                  </>
                  )}
                </RadioGroup>
              }
              name="worship"
              control={control}
              defaultValue={""}
              rules={{ required: true }}
            />
            {errors.worship && <div style={{color: 'red', marginTop: 5, marginLeft: 5}}> </div>}
          </InputContainer>
          <InputContainer>
            <SubmitButton type="submit" disabled={setting.isSubmitting}></SubmitButton>
          </InputContainer>
        </InserForm>

      ) : (
        <ResultContainer result={result}>
        {result == null ? (
          <div>  ...</div>
        ) : (
          <>
          {result === '' ? (
            <>
            <div style={{fontSize: '1.5rem', marginBottom: '0.5rem'}}>{userInfo.name} {userInfo.position} </div>
            <div style={{fontSize: '1.5rem', marginBottom: '1rem'}}>.</div>
            {setting.isWeekday ? (
              <div style={{fontSize: '1.5rem', marginBottom: '0.5rem', color: '#F54E50'}}>{userInfo.title}  .</div>
            ) : (
              <>
              <div style={{fontSize: '1.5rem', marginBottom: '0.5rem', color: '#F54E50'}}>{userInfo.title} {userInfo.serviceTime}</div>
              <div style={{fontSize: '1.5rem', marginBottom: '0.5rem', color: '#F54E50'}}> .</div>
              </>
            )}
            </>
          ) : (
            <>
            <div style={{fontSize: '1.5rem', marginBottom: '1rem'}}>{userInfo.name} {userInfo.position} </div>
            {setting.isWeekday ? (
              <div style={{fontSize: '1.5rem', marginBottom: '0.5rem', color: handleColorType(result)}}>{userInfo.title} {result} .</div>
            ) : (
              <>
              <div style={{fontSize: '1.5rem', marginBottom: '0.5rem', color: handleColorType(result)}}>{userInfo.title} {userInfo.serviceTime}</div>
              <div style={{fontSize: '1.5rem', marginBottom: '0.5rem', color: handleColorType(result)}}>{result} .</div>
              </>
            )}
            <div style={{fontSize: '1.25rem', marginBottom: '2rem'}}>   </div>
            </>
          )}
          <ReturnButton 
            onClick={() => onCloseClick()}
          >
              
          </ReturnButton>
          </>
        )}
        </ResultContainer>
      )}
    </Container>
  )
}



const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const Header = styled.div`
  display: flex;
  justify-content: center;
  margin: 1.5rem 0 0;
  font-size: 2rem;
  font-weight: 700;
`;

const CommentContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 90%;
  margin: 2rem 0 1.25rem;
`;

const CommentHeader = styled.div`
  display: flex;
  width: 100%;
  background-color: #F2B49B;
  align-items: center;
  color: #fff;
  padding: 0.5rem;
  box-sizing: border-box;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
`;

const CommentBody = styled.div`
  width: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 0.75rem;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0.25px 1px gray;
`

const InserForm = styled.form`
  padding: 1rem;
  width: 80%;
  display: flex;
  justify-content: center;
`;

const InputContainer = styled.section`
  display: flex;
  flex-direction: column;
  margin: 1rem 0;
`;

const Title = styled.label`
  width: 100%;
  margin-bottom: 0.4rem;
  font-size: 1rem;
  color: #222;
`;

const Input = styled.input`
  width: 100%;
  border-radius: 6px;
  border: 2px solid #228be6;
  padding: 0.75rem;
  outline: none;
  font-size: 1rem;
  box-sizing: border-box;
`;

const SubmitButton = styled.button`
  width: 100%;
  padding: 0.75rem;
  margin: 0.75rem 0;
  color: #fff;
  background-color: #228be6;
  border: 2px solid #228be6;
  border-radius: 4px;
  outline: none;
  box-sizing: border-box;
  font-size: 1rem;
  font-weight: 600;
`;

const ResultContainer = styled.div`
  width: 85%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 16px;
  padding: 1rem;
  margin-top: 2rem;
  border: 2px solid ${props => handleColorType(props.result)};
  box-sizing: border-box;
`;

const ReturnButton = styled.button`
  padding: 0.75rem;
  border-radius: 8px;
  font-size: 1.1rem;
  margin-top: 1.25rem;
`;

const handleColorType = result => {
    switch (result){
      case '':
        return "#4FDAFF";
      case '':
        return "#57FFAD";
      case '':
        return "#F54E50";
      default:
        return "#222222";
    }
  }

export default SelfCheckPage;