import React, { useState, useEffect } from 'react';
import styled from 'styled-components'
import { db } from '../config/firebaseConfig'

import NavBarForAdmin from '../components/NavBarForAdmin'

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

function fillZero(width, str){
  return str.length >= width ? str:new Array(width-str.length+1).join('0')+str;//  0 
}

const AdminPage = () => {
  const [ userlist, setUserlist ] = useState(false)
  const [ latestSetting, setLatestSetting ] = useState(null)
  const [ forcingCloseWed, setForcingCloseWed ] = useState(false)
  const [ forcingCloseFri, setForcingCloseFri ] = useState(false)
  const [ forcingCloseSun, setForcingCloseSun ] = useState(false)
  const [ seats, setSeats ] = useState({
    wednesday: 90,
    friday: 90,
    sundayOne: 90,
    sundayTwo: 90,
    sundayThree: 90,
    sundayFour: 90,
    sundaySix: 90,
  })
  const [ comments, setComments ] = useState({
    commentWed: "",
    commentFri: "",
    commentSunLineOne: "",
    commentSunLineTwo: "",
    commentSunLineThree: "",
    commentOverviewLineOne: "",
    commentOverviewLineTwo: "",
    commentOverviewLineThree: "",
  })

  const loadSettings = () => {
    db.collection('setting').doc('latest').get().then((doc) => {
      if (doc.exists) {
        console.log("Setting data:", doc.data());
        setLatestSetting(doc.data())
      } else {
        console.log("No such document!");
      }
    }).catch((error) => {
      console.log("Error @loadSettings: ", error)
    })
  }

  useEffect(() => {
    console.log('  ')
    if (latestSetting === null) {
      loadSettings()
    }
  }, [latestSetting])



  const batch = db.batch();
  
  let wednesday = new Date();
  wednesday.setDate(wednesday.getDate() + (3 + 7 - wednesday.getDay()) % 7);
  const wednesdayString = `${wednesday.getFullYear()}${fillZero(2, (wednesday.getMonth()+1).toString())}${fillZero(2, (wednesday.getDate()).toString())}`

  let friday = new Date();
  friday.setDate(friday.getDate() + (5 + 7 - friday.getDay()) % 7);
  const fridayString = `${friday.getFullYear()}${fillZero(2, (friday.getMonth()+1).toString())}${fillZero(2, (friday.getDate()).toString())}`

  let sunday = new Date();
  sunday.setDate(sunday.getDate() + (0 + 7 - sunday.getDay()) % 7);
  const sundayString = `${sunday.getFullYear()}${fillZero(2, (sunday.getMonth()+1).toString())}${fillZero(2, (sunday.getDate()).toString())}`

  const onClickNotification = () => {
    const settingRef = db.collection('setting').doc('latest')
    settingRef.update({
      commentOverviewLineOne: comments.commentOverviewLineOne,
      commentOverviewLineTwo: comments.commentOverviewLineTwo,
      commentOverviewLineThree: comments.commentOverviewLineThree,
    })
    .then(() => {
      console.log("Document successfully written!");
      alert('  ')
    })
    .catch((error) => {
      console.error("Error writing document: ", error);
      alert('  ')
    });
  }

  const onClickWeekdayNotification = () => {
    const settingRef = db.collection('setting').doc('latest')
    settingRef.update({
      commentWed: comments.commentWed,
      commentFri: comments.commentFri,
    })
    .then(() => {
      console.log("Document successfully written!");
      alert('   ')
    })
    .catch((error) => {
      console.error("Error writing document: ", error);
      alert('   ')
    });
  }

  const onClickSundayNotification = () => {
    const settingRef = db.collection('setting').doc('latest')
    settingRef.update({
      commentSunLineOne: comments.commentSunLineOne,
      commentSunLineTwo: comments.commentSunLineTwo,
      commentSunLineThree: comments.commentSunLineThree,
    })
    .then(() => {
      console.log("Document successfully written!");
      alert('   ')
    })
    .catch((error) => {
      console.error("Error writing document: ", error);
      alert('   ')
    });
  }

  const onClickWeekday = () => {
      const wedRef = db.collection('').doc('1').collection(wednesdayString).doc('--stats--')
      const friRef = db.collection('').doc('1').collection(fridayString).doc('--stats--')
      const settingRef = db.collection('setting').doc('latest')
      batch.set(wedRef, { ReservationCount: parseInt(seats.wednesday) })
      batch.set(friRef, { ReservationCount: parseInt(seats.friday) })
      batch.update(settingRef, {
        isWeekday: true,
        wednesday: wednesdayString,
        friday: fridayString,
        sunday: "",
        TimerWednesDay: [wednesday.getFullYear(), wednesday.getMonth(), wednesday.getDate()-1, 19, 0],
        TimerFriDay: [friday.getFullYear(), friday.getMonth(), friday.getDate()-3, 20, 0],
        TimerSunDay: "",
        forcingCloseWed: forcingCloseWed,
        forcingCloseFri: forcingCloseFri,
        wednesDaySeats: parseInt(seats.wednesday),
        fridaySeats: parseInt(seats.friday),
        sundayOneSeats: "",
        sundayTwoSeats: "",
        sundayThreeSeats: "",
        sundayFourSeats: "",
        sundaySixSeats: "",
        commentSunLineOne: "",
        commentSunLineTwo: "",
        commentSunLineThree: "",

      })
      batch.commit()
      .then(() => {
        console.log("Document successfully written!");
        alert(' ')
      })
      .catch((error) => {
        console.error("Error writing document: ", error);
        alert(' ')
      });
  }


  const onClickSunDay = () => {
    const firstRef = db.collection('').doc('1').collection(sundayString).doc('--stats--')
    const secondRef = db.collection('').doc('2').collection(sundayString).doc('--stats--')
    const thirdRef = db.collection('').doc('3').collection(sundayString).doc('--stats--')
    const forthRef = db.collection('').doc('4').collection(sundayString).doc('--stats--')
    const sixthRef = db.collection('').doc('6').collection(sundayString).doc('--stats--')
    const settingRef = db.collection('setting').doc('latest')
    batch.set(firstRef, { ReservationCount: parseInt(seats.sundayOne) })
    batch.set(secondRef, { ReservationCount: parseInt(seats.sundayTwo) })
    batch.set(thirdRef, { ReservationCount: parseInt(seats.sundayThree) })
    batch.set(forthRef, { ReservationCount: parseInt(seats.sundayFour) })
    batch.set(sixthRef, { ReservationCount: parseInt(seats.sundaySix) })
    batch.update(settingRef, {
      isWeekday: false,
      wednesday: "",
      friday: "",
      sunday: sundayString,
      TimerWednesDay: "",
      TimerFriDay: "",
      TimerSunDay: [sunday.getFullYear(), sunday.getMonth(), sunday.getDate()-3, 19, 0],
      forcingCloseWed: false,
      forcingCloseFri: false,
      wednesDaySeats: "",
      fridaySeats: "",
      sundayOneSeats: parseInt(seats.sundayOne),
      sundayTwoSeats: parseInt(seats.sundayTwo),
      sundayThreeSeats: parseInt(seats.sundayThree),
      sundayFourSeats: parseInt(seats.sundayFour),
      sundaySixSeats: parseInt(seats.sundaySix),
      commentWed: "",
      commentFri: "",
    })
    batch.commit()
    .then(() => {
      console.log("Document successfully written!");
      alert(' ')
    })
    .catch((error) => {
      console.error("Error writing document: ", error);
      alert(' ')
    });
  }

  const onClickLookUp = (title, time, date) => {
    db.collection(title).doc(time).collection(date).orderBy("serverTime").get().then((querySnapshot) => {
      let tempArray = []
       querySnapshot.forEach((doc) => {
         tempArray.push({
          id: doc.id,
          ...doc.data()
         })
      })
      setUserlist(tempArray)
    }).catch(error => {
      console.log(error)
    })
  }

  const converTime = (timestamp) => {
    let date = new Date(timestamp * 1000)
    let hours = date.getHours();
    let minutes = "0" + date.getMinutes();
    let seconds = "0" + date.getSeconds();
    let formattedTime = hours + ':' + minutes.substr(-2) + ':' + seconds.substr(-2);
    return formattedTime
  }

  const onForingCloseClick = (days) => {
    if (days === "") {
      setForcingCloseWed(!forcingCloseWed)
    } else if (days === "") {
      setForcingCloseFri(!forcingCloseFri)
    } else if (days === "") {
      setForcingCloseSun(!forcingCloseSun)
    }
  }

  const onSeatsChange = (e) => {
    const { value, name } = e.target
    setSeats({
      ...seats,
      [name]: value
    })
  }

  const onCommentChange = (e) => {
    const { value, name } = e.target
    setComments({
      ...comments,
      [name]: value
    })
  }


  return (
    <Container>
      <NavBarForAdmin />

      <div style={{fontSize: 16, fontWeight: 600, marginBottom: 24, marginTop: 24, backgroundColor:'lightgray', padding: 10}}>  </div>
        <div>{latestSetting==null ? "" : latestSetting.isWeekday ? " (/)  ." : "  ."}</div>


      <div style={{fontSize: 16, fontWeight: 600, marginBottom: 24, marginTop: 24, backgroundColor:'lightgray', padding: 10}}></div>
      <div style={{display:'flex', flexDirection:'row', alignItems: 'center'}}>
        <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: '15%', alignItems: 'center'}}></div>
        <div style={{display:'flex', flexDirection:'column', alignItems: 'center', width: '60%', marginRight: 10}}>
          <input 
            name="commentOverviewLineOne" 
            value={comments.commentOverviewLineOne} 
            onChange={onCommentChange}
            placeholder="  " 
            style={{ width: '100%', height: '1.5rem', marginBottom: 5 }}
          />
          <input 
            name="commentOverviewLineTwo" 
            value={comments.commentOverviewLineTwo} 
            onChange={onCommentChange} 
            placeholder="  " 
            style={{ width: '100%', height: '1.5rem', marginBottom: 5 }}
          />
          <input 
            name="commentOverviewLineThree" 
            value={comments.commentOverviewLineThree} 
            onChange={onCommentChange} 
            placeholder="  " 
            style={{ width: '100%', height: '1.5rem', marginBottom: 5 }}
          />
        </div>
        <button 
          style={{backgroundColor: 'red', border: '2px solid black', padding: 10, font: 20, fontWeight: 600}}
          onClick={() => onClickNotification()}
        >
           
        </button>
      </div>
      

      <div style={{fontSize: 16, fontWeight: 600, marginBottom: 24, marginTop: 24, backgroundColor:'lightgray', padding: 10}}>  </div>
      <SettingContainer>
        <div></div>
        <Setting>
          <div></div>
          <Title> : {wednesday.getFullYear()} {wednesday.getMonth()+1} {wednesday.getDate()}</Title>
          <Title> : {forcingCloseWed ? '' : `${wednesday.getFullYear()} ${wednesday.getMonth()+1} ${wednesday.getDate()-1} 19`}</Title>
          <Title> : {seats.wednesday}</Title>
        </Setting>
        <Setting>
          <div></div>
          <Title> : {friday.getFullYear()} {friday.getMonth()+1} {friday.getDate()}</Title>
          <Title> : {forcingCloseFri ? '' : `${friday.getFullYear()} ${friday.getMonth()+1} ${friday.getDate()-3} 20`}</Title>
          <Title> : {seats.friday}</Title>
        </Setting>
        <div style={{borderTop: '1px solid black', marginBottom: 10}}/>
        <div>
          <div style={{display: 'flex', flexDirection: 'row', marginBottom: 10, alignItems: 'center'}}>
            <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: 60}}> </div>
            <input name="wednesday" value={seats.wednesday} onChange={onSeatsChange} style={{width: 50}}/>
            <div style={{marginLeft: 10, marginRight: 10, display:'flex', flexWrap:'wrap', width: 60}}> </div>
            <input name="friday" value={seats.friday} onChange={onSeatsChange} style={{width: 50, marginRight: 30}}/>
            <button onClick={() => onForingCloseClick('')} style={{marginRight: 20}}> </button>
            <button onClick={() => onForingCloseClick('')}> </button>
          </div>
          <button 
            style={{backgroundColor: 'red', border: '2px solid black', padding: 10, font: 20, fontWeight: 600}}
            onClick={() => onClickWeekday()}
          >
                
          </button>

          <div style={{display:'flex', marginBottom: 10, marginTop: 20, flexDirection:'column'}}>
          <div style={{display:'flex', marginBottom: 10, flexDirection:'row'}}>
            <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: '25%', alignItems: 'center'}}> </div>
              <input 
                name="commentWed" 
                value={comments.commentWed} 
                onChange={onCommentChange} 
                style={{width: '70%', height: '1.5rem'}}
              />
          </div>
          <div style={{display:'flex', marginBottom: 10, flexDirection:'row'}}>
            <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: '25%', alignItems: 'center'}}> </div>
              <input 
                name="commentFri" 
                value={comments.commentFri} 
                onChange={onCommentChange} 
                style={{width: '70%', height: '1.5rem'}}
              />
            </div>
          </div>
          <button 
            style={{
              backgroundColor: 'red', border: '2px solid black', padding: 10, font: 20, fontWeight: 600,
              display:'flex', flexWrap:'wrap', justifyContent: 'center'
            }}
            onClick={() => onClickWeekdayNotification()}
          >
              
          </button>
        </div>
      </SettingContainer>
      <div style={{borderTop: '2px solid black', marginBottom: 10}}/>
      <SettingContainer>
        <div></div>
        <Setting>
          <Title> : {sunday.getFullYear()} {sunday.getMonth()+1} {sunday.getDate()}</Title>
          <Title> : {forcingCloseWed ? '' : `${sunday.getFullYear()} ${sunday.getMonth()+1} ${sunday.getDate()-3} 19`}</Title>
        </Setting>
        <Setting>
          <Title>1  : {seats.sundayOne}</Title>
          <Title>2  : {seats.sundayTwo}</Title>
          <Title>3  : {seats.sundayThree}</Title>
          <Title>4  : {seats.sundayFour}</Title>
          <Title>6  : {seats.sundaySix}</Title>
        </Setting>
        <div style={{borderTop: '1px solid black', marginBottom: 10}} />
        <div>
          <div style={{display: 'flex', flexDirection: 'row', marginBottom: 10, alignItems: 'center'}}>
            <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: 60}}>1 </div>
            <input name="sundayOne" value={seats.sundayOne} onChange={onSeatsChange}  style={{width: 30, marginRight: 10}}/>
            <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: 60}}>2 </div>
            <input name="sundayTwo" value={seats.sundayTwo} onChange={onSeatsChange}  style={{width: 30, marginRight: 10}}/>
            <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: 60}}>3 </div>
            <input name="sundayThree" value={seats.sundayThree} onChange={onSeatsChange}  style={{width: 30, marginRight: 10}}/>
            <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: 60}}>4 </div>
            <input name="sundayFour" value={seats.sundayFour} onChange={onSeatsChange} style={{width: 30, marginRight: 10}}/>
            <div style={{marginRight: 10, display:'flex', flexWrap:'wrap', width: 60}}>6 </div>
            <input name="sundaySix" value={seats.sundaySix} onChange={onSeatsChange} style={{width: 30, marginRight: 10}}/>
          </div>

          <button 
            style={{backgroundColor: 'red', border: '2px solid black', padding: 10, font: 20, fontWeight: 600}}
            onClick={() => onClickSunDay()}
          >
                
          </button>

          <div style={{display:'flex', flexDirection:'row', alignItems: 'center', marginTop: 20}}>
            <div style={{marginRight: 10, display: 'flex', flexDirection:'column', width: '15%', alignItems: 'center'}}>
              <span></span>
              <span></span>
            </div>
            <div style={{display:'flex', flexDirection:'column', alignItems: 'center', width: '60%', marginRight: 10}}>
              <input 
                name="commentSunLineOne" 
                value={comments.commentSunLineOne} 
                onChange={onCommentChange} 
                placeholder="  "
                style={{ width: '100%', height: '1.5rem', marginBottom: 5 }}
              />
              <input 
                name="commentSunLineTwo" 
                value={comments.commentSunLineTwo} 
                onChange={onCommentChange} 
                placeholder="  "
                style={{ width: '100%', height: '1.5rem', marginBottom: 5 }}
              />
              <input 
                name="commentSunLineThree" 
                value={comments.commentSunLineThree} 
                onChange={onCommentChange} 
                placeholder="  "
                style={{ width: '100%', height: '1.5rem', marginBottom: 5 }}
              />
            </div>
            <button 
              style={{
                backgroundColor: 'red', border: '2px solid black', padding: 10, font: 20, fontWeight: 600,
              }}
              onClick={() => onClickSundayNotification()}
            >
               
            </button>
          </div>
        </div>
      </SettingContainer>

      <div style={{fontSize: 16, fontWeight: 600, marginBottom: 28, marginTop: 28, backgroundColor:'lightgray', padding: 10}}>  </div>
      
      <LookupContainer>
        <LookupButton onClick={() => onClickLookUp('', '1', wednesdayString)}></LookupButton>
        <LookupButton onClick={() => onClickLookUp('', '1', fridayString)}></LookupButton>
        <LookupButton onClick={() => onClickLookUp('', '1', sundayString)}>1</LookupButton>
        <LookupButton onClick={() => onClickLookUp('', '2', sundayString)}>2</LookupButton>
        <LookupButton onClick={() => onClickLookUp('', '3', sundayString)}>3</LookupButton>
        <LookupButton onClick={() => onClickLookUp('', '4', sundayString)}>4</LookupButton>
        <LookupButton onClick={() => onClickLookUp('', '6', sundayString)}>6</LookupButton>
      </LookupContainer>
      

      <TableContainer component={Paper}>
        <Table aria-label="simple table" style={{minWidth: 250}}>
          <TableHead>
            <TableRow>
              <TableCell align="center"></TableCell>
              <TableCell align="center"></TableCell>
              <TableCell align="center"></TableCell>
              <TableCell align="center">()</TableCell>
              <TableCell align="center"></TableCell>
              <TableCell align="center"></TableCell>
            </TableRow>
          </TableHead>
          {userlist && (
            <>
            {userlist.map((user, index) => (
              <TableBody>
                <TableRow key={index}>
                  <TableCell align="center">{user.index}</TableCell>
                  <TableCell align="center">{user.name}</TableCell>
                  <TableCell align="center">{user.position}</TableCell>
                  <TableCell align="center">{user.division}</TableCell>
                  <TableCell align="center">{converTime(user.serverTime)}</TableCell>
                  <TableCell align="center">
                    {user.index < 71 ? '' : user.index >= 71 && user.index < 91 ? '' : '' }
                  </TableCell>
                </TableRow>
              </TableBody>
            ))}  
            </>
          )}
        </Table>
      </TableContainer>
    </Container>
    
  )
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  /* justify-content: center;
  align-items: center; */
`;

const SettingContainer = styled.div`
  margin-bottom: 20px;
`;

const Setting = styled.div`
  display: flex;
  align-items: center;
  margin: 10px 0;
`;

const Title = styled.div`
  display: flex;
  align-items: center;
  padding-left: 1rem;
`;

const LookupContainer = styled.div`
  margin-bottom: 1rem;
  display: flex;
`;

const LookupButton = styled.button`
  border: 2px solid #228be6;
  padding: 1rem;
  margin: 0 1rem;
`;




export default AdminPage;