import React, { useContext } from 'react';
import { useForm, Controller } from "react-hook-form";
import { useHistory } from 'react-router-dom';
import styled from 'styled-components'
import { SettingContext } from '../../context/SettingContext'

import NavBar from '../../components/NavBar'
import { Radio, RadioGroup, FormControlLabel} from '@material-ui/core';


const SundayService = () => {
  const [ setting, setSetting ] = useContext(SettingContext)
  const { register, handleSubmit, errors, control } = useForm()
  const history = useHistory();

  const onSubmit = (data) => {
    if (!setting.isSubmitting) {

      setSetting({
        ...setting,
        isSubmitting: true,
      })

      const submitData = {
        ...data,
        title: '',
        reservationDate: setting.sunday,
        submitTime: new Date()
      }
      history.push({
        pathname: "/service-register/result", 
        payload: submitData
      })
      console.log(submitData)
      console.log('')
    } else {
      console.log('')
    }
  }

  let latestDay = new Date();
  latestDay.setDate(latestDay.getDate() + (0 + 7 - latestDay.getDay()) % 7);

  return (
    <Container>
      <NavBar />
      <Header>{latestDay.getMonth()+1} {latestDay.getDate()}  </Header>
      <InserForm onSubmit={handleSubmit(onSubmit)} style={{display:'flex', flexDirection:'column'}}>
        <InputContainer>
          <Title></Title>
          <Input name="name" placeholder=" " ref={register({ required: true, maxLength: 4 })}/>
          {errors.name && <div style={{color: 'red', marginTop: 5, marginLeft: 5}}>   </div>}
        </InputContainer>
        <InputContainer>
          <Title></Title>
          <Input name="position" placeholder="/////" ref={register({ required: true})} />
          {errors.position && <div style={{color: 'red', marginTop: 5, marginLeft: 5}}> </div>}
        </InputContainer>
        <InputContainer>
          <Title></Title>
          <Input name="division" placeholder="/////" ref={register({ required: true})}/>
          {errors.division && <div style={{color: 'red', marginTop: 5, marginLeft: 5}}> </div>}
        </InputContainer>
        <InputContainer>
          <Title></Title>
          <Controller
            as={
              <RadioGroup aria-label="" style={{paddingLeft: '1rem'}} >
                <FormControlLabel
                  value="1"
                  control={<Radio />}
                  label="1  ( 7 10)"
                />
                <FormControlLabel
                  value="2"
                  control={<Radio />}
                  label="2 ( 8 00)"
                />
                <FormControlLabel
                  value="3"
                  control={<Radio />}
                  label="3 ( 9 30)"
                />
                <FormControlLabel
                  value="4"
                  control={<Radio />}
                  label="4 ( 11 00)"
                />
                <FormControlLabel
                  value="6"
                  control={<Radio />}
                  label="6  ( 4 00)"
                />
              </RadioGroup>
            }
            name="serviceTime"
            control={control}
            defaultValue={"1"}
            rules={{ required: true }}
          />
          {errors.serviceTime && <div style={{color: 'red', marginTop: 5, marginLeft: 5}}> </div>}
        </InputContainer>
        <InputContainer>
          <SubmitButton type="submit" disabled={setting.isSubmitting}></SubmitButton>
        </InputContainer>
      </InserForm>
    </Container>
  )
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;

const Header = styled.div`
  display: flex;
  justify-content: center;
  margin: 1.5rem 0 0;
  font-size: 2rem;
  font-weight: 700;
`;

const InserForm = styled.form`
  padding: 1rem;
  width: 80%;
  display: flex;
  justify-content: center;
`;

const InputContainer = styled.section`
  display: flex;
  flex-direction: column;
  margin: 1rem 0;
`;

const Title = styled.label`
  width: 100%;
  margin-bottom: 0.4rem;
  font-size: 1rem;
  color: #222;
`;

const Input = styled.input`
  width: 100%;
  border-radius: 6px;
  border: 2px solid #228be6;
  padding: 0.75rem;
  outline: none;
  font-size: 1rem;
  box-sizing: border-box;
`;

const SubmitButton = styled.button`
  width: 100%;
  padding: 0.75rem;
  margin: 0.75rem 0;
  color: #fff;
  background-color: #228be6;
  border: 2px solid #228be6;
  border-radius: 4px;
  outline: none;
  box-sizing: border-box;
  font-size: 1rem;
  font-weight: 600;
`;

export default SundayService;